import * as Yup from "yup";

const phoneRegExp =
  /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/;
// const number = /^[0-9]{9,12}$/i;

export const RegisterSchema = Yup.object().shape({
  // password: Yup.string()
  //   .required("Bt buc")
  //   .min(6, "Ti thiu 6 k t")
  //   .max(30, "Qu di"),

  username: Yup.string().required("Bt buc"),
  email: Yup.string().email("Sai nh dng").required("Bt buc"),
  password: Yup.string().required("Bt buc").min(6, "Mt Khu phi t 8-16 k t"),
  // .matches(
  //   /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*()_+])/,
  //   "Mt Khu phi t 8-16 k t, bao gm ch ci in hoa, ch ci in thng, k t c bit v con s."
  // ),
  password_confirm: Yup.string()
    .required("Bt buc")
    .oneOf([Yup.ref("password"), null], "Khng trng"),
  // gender: Yup.string()
  //   .oneOf(["1", "2", "3"], "Bn khng th  trng Field ny")
  //   .required(Messages.bat_buoc),
  // birthday: Yup.string().nullable(),
  phone: Yup.string()
    .matches(phoneRegExp, "Sai nh dng")
    .required("Bt buc"),
});
