import React, { useEffect, useState } from "react";
import "./header_navbar.scss";
import {useNavigate} from "react-router-dom"
const HeaderNavbar = (prop) => {
  const currenUser = localStorage.getItem("customerName");
  const [textLogin, setTextLogin] = useState("ng nhp");
  const navigate = useNavigate()

  const register = () => {
    prop.passCheckRegister(true);
  };

  const login = () => {
    prop.passCheckLogin(true);
  };

  const logOut = () => {
    localStorage.clear();
    setTextLogin("ng nhp");
    prop.checkLogout(true)
    navigate("/bidu-ecommerce")
  };

  useEffect(() => {
    if (currenUser) {
      setTextLogin("ng xut");
    }
  },[currenUser]);

  return (
    <div className="header__container">
      <div className="header__main">
        <div className="col left">
          <p>Trang ngi bn</p>
          <p>Danh mc</p>
        </div>
        <div className="col right">
          <p>Vit Nam</p>
          <span></span>
          {currenUser ? <p style={{color: "rgb(187, 58, 165)"}}>{currenUser}</p> : <p onClick={register}>ng k</p>}
          <span></span>
          <p onClick={textLogin === "ng nhp" ? login : logOut}>
            {textLogin}
          </p>
        </div>
      </div>
    </div>
  );
};

export default HeaderNavbar;
